.address{
  flex-grow: 1;
  background: #F2F2F2;
  padding: 40px 0 55px;
  &__inner{
    display: grid;
    grid-template-columns: calc(100% - 600px) 550px;
    justify-content: space-between;
    gap: 50px;
    margin-top: 20px;
    max-width: 1295px;
  }
}

.address-card{
  background: #FAFAF9;
  border-radius: 8px;
  filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));
  max-width: 654px;
  padding: 20px 20px 70px;
  cursor: pointer;
  &:not(:last-child){
    margin-bottom: 34px;
  }
  &__top{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__desc{
    margin-top: 14px;
    padding: 11px 0;
    font-size: 12.6538px;
    line-height: 221%;
    border-bottom: 1px solid #DADADA;
    max-width: 340px;
  }
}

.address-form{
  padding: 46px 60px 40px;
  background: #FFFFFF;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  border-radius: 8px;
  &__inner{
    max-width: 340px;
  }
  &__desc{
    margin-top: 14px;
    padding: 11px 0;
    font-size: 12.6538px;
    line-height: 221%;
    border-bottom: 1px solid #DADADA;
  }
  &__map{
    margin-top: 15px;
    padding-bottom: 33%;
    position: relative;
    overflow: hidden;
    img{
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      position: absolute;
      display: block;
    }
  }
  &__grid{
    margin-top: 16px;
    display: grid;
    grid-template-columns: 1fr 1.8fr 1.4fr 1.4fr;
    gap: 20px;
  }
  &__full{
    grid-column: 1/5;
  }
  &__buttons{
    margin-top: 100px;
  }
  &__btn{
    margin-top: 15px;
    width: 100%;
    height: 48px;
    border-radius: 4px;
  }
}


@media screen and (max-width: 1200px){
  .address{
    &__inner{
      grid-template-columns: calc(100% - 450px) 430px;
      gap: 20px;
    }
  }

  .address-form{
    padding-left: 45px;
    padding-right: 45px;
  }
}


@media screen and (max-width: 992px){
  .address{
    background-color: transparent;
    position: relative;
    padding: 20px 0 30px;
    &__inner{
      grid-template-columns: 100%;
    }
  }

  .address-card{
    position: relative;
    padding: 0;
    background-color: transparent;
    box-shadow: none;
    max-width: 100%;
    &:not(:last-child){
      margin-bottom: 13px;
    }
    &__top{
      svg{
        position: absolute;
        right: 11px;
        bottom: 16px;
      }
    }
    &__title{
      display: none;
    }
    &__desc{
      max-width: 100%;
      margin: 0;
    }
  }

  .address-form{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    min-height: 100%;
    box-shadow: none;
    padding: 20px 20px 30px;
    opacity: 0;
    pointer-events: none;
    transition: all .3s;
    &--active{
      opacity: 1;
      pointer-events: unset;
    }
    &__inner{
      max-width: 100%;
    }
  }
}
