@import "../vars";
.mbtn {
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all ease-out .225s;
    padding: 0;
    border: none;
    background-color: transparent;
    cursor: pointer;
    &:focus {
        box-shadow: none;
    }
}

.mbtn-gray{
    background: #D9D9D9;
    border-radius: 4px;
    color: #fff;
    &:hover{
        background: darken(#D9D9D9,15%);
        color: #fff;
    }
}

.mbtn-accent{
    background: $primary;
    border-radius: 4px;
    color: #fff;
    &:hover{
        background: darken($primary,10%);
        color: #fff;
    }
}
